.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.faq-section {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.faq-question {
  width: calc(50% - 20px);
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .faq-question {
    width: 100%;
  }
}

.faq-question-button {
  background-color: #23354E;
  color: #ffffff;
  width: 100%;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-right: 10px;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.res-question-button {
  background-color: #23354E;
  color: #ffffff;
  width: 90%;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-right: 10px;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.faq-question-button:hover {
  background-color: #22C3FF;
}

.faq-question-button:active {
  transform: translateY(2px);
}

.faq-question-button svg {
  width: 20px;
  height: 20px;
  fill: #ffffff;
  transition: transform 0.3s ease;
}

.faq-question-button:hover svg {
  transform: rotate(45deg);
}

.faq-question-button.expanded svg {
  transform: rotate(-135deg);
}

.faq-answer {
  background-color: #ffffff;
  padding: 20px;
  font-size: 16px;
  color: #23354E;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  line-height: 1.5;
}

.res-answer {
  background-color: #ffffff;
  padding: 18px;
  width: 90%;
  text-align: justify;
  font-size: 16px;
  color: #23354E;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  line-height: 1.5;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem;
  color: #fff;
  border-radius: 4px;
}

.popup button {
  background-color: #22C3FF;
  border: none;
  color: #fff;
  font-size: 1.2rem;
  float: right;
  cursor: pointer;
  justify-content: right;
}

.parallax {
  background-image: url('../src/assets/images/eva_main.jpg');

  height: 100%; 

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media only screen and (max-device-width: 1366px) {
  .parallax {
    background-attachment: scroll;
  }
}